<script>
import CreateNew from '@components/createNew.vue';

Date.prototype.format = function format(str) {
  let fmt = str;
  const o = {
    'M+': this.getMonth() + 1, // 
    'd+': this.getDate(), // 
    'h+': this.getHours(), // 
    'm+': this.getMinutes(), // 
    's+': this.getSeconds(), // 
    'q+': Math.floor((this.getMonth() + 3) / 3), // 
    S: this.getMilliseconds(), // 
  };
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (`${this.getFullYear()}`).substr(4 - RegExp.$1.length));
  }
  for (const key in o) {
    if (new RegExp(`(${key})`).test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[key]) : ((`00${o[key]}`).substr((`${o[key]}`).length)));
    }
  }
  return fmt;
};

export default {
  name: 'projectIndex',
  props: {
    get_projects: {
      type: Function,
      default: () => Function,
    },
    projectlist: {
      type: Array,
    },
    service_id: {
      type: Number,
    },
  },
  components: {
    CreateNew,
  },
  data() {
    return {
      list: [],
    };
  },
  methods: {
    addNewProject() {
      this.$refs.createnew.addProject(this.service_id);
    },
    toDetail(item) {
      this.$router.push({ path: '/dashboard/detail/prosetting', query: { project_id: item.project_id,service_id:this.service_id } });
    },
    DateFormat(date) {
      return new Date(date).format('yyyy-MM-dd hh:mm:ss');
    },
  },
  mounted() {
    this.get_projects();
  },
};
</script>

