<template>
  <ConfirmLayer v-if="isConfirm" @closeLayer="cancelCreate">
    <span slot="confirmtitle">{{$t('createNew')}}</span>
    <div  slot="confirmcontent">
      <div>
        <div class="flex flex-wrap justify-between"><label class="pr-2 mb-2">{{$t('projectName')}}<span
          class="text-pink">*</span></label></div>
        <input
          class="h-10 flex-1 block w-full border border-gray-400 placeholder-gray-400 rounded-sm"
          type="text" name="name" v-model="project_name">
        <div class="flex justify-end items-end flex-wrap mt-6 sm:flex-no-wrap">
          <button
            class="inline-flex items-center justify-center font-mono uppercase transition ease-in duration-200 hover:no-underline active:shadow-inner focus:outline-none rounded-sm h-10 px-4 text-sm border border-gray-400 text-gray-600 shadow-none hover:bg-gray-200 active:bg-gray-300 mr-4" @click="cancelCreate">{{$t('cancel')}}</button>
          <button
            class="inline-flex items-center justify-center font-mono uppercase transition ease-in duration-200 hover:no-underline active:shadow-inner focus:outline-none rounded-sm h-10 px-4 text-sm border text-white shadow hover:shadow-hover hover:border-transparent active:border-transparent bg-black border-black hover:text-pink"
          @click="submitCreate">{{$t('createBtn')}}</button>
        </div>
      </div>
    </div>
  </ConfirmLayer>
</template>

<script>
import mixin from '@mixin';
import ConfirmLayer from '@components/ConfirmLayer.vue';
import layer from '@/utils/layer';
import api from '@/api';

export default {
  name: 'createNew',
  mixins: [mixin],
  data() {
    return {
      project_name: '',
      service_id: '',
    };
  },
  components: {
    ConfirmLayer,
  },
  methods: {
    cancelCreate() {
      this.removeConfirm();
    },
    addProject(service_id) {
      this.service_id = service_id;
      this.addConfirm();
    },
    submitCreate() {
      const { project_name, service_id } = this;
      const obj = {
        project_name,
        service_id,
      };
      if (this.project_name.length) {
        api.projectApi.create_project(obj).then((result) => {
          if (!result.success) {
            layer.tip(result.error, 'error');
          } else {
            layer.tip(this.$t('createsuccess'));
            setTimeout(() => {
              this.cancelCreate();
              window.location.reload();
            }, 1000);
          }
        });
      }
    },
  },
  mounted() {
  },
};
</script>

