<template>
<div class="flex-1 relative z-0 overflow-y-auto focus:outline-none">
    <no-heco v-if="!projectList.length && service_id !== 2"></no-heco>
    <no-bsc v-if="!projectList.length && service_id === 2"></no-bsc>
    <project-index v-if="projectList.length" :projectlist="projectList" :service_id="service_id"></project-index>

</div>
</template>

<script>
import ProjectIndex from '@components/project/projectIndex.vue';
import NoHeco from '@components/noProject/noHeco.vue';
import NoBsc from '@components/noProject/noBSC.vue';
import api from '@/api';

export default {
  name: 'project',
  components: {
    ProjectIndex,
    NoHeco,
    NoBsc,
  },
  data() {
    return {
      projectList: [],
      service_id: 1,
    };
  },
  watch: {
    $route(to) {
      this.toActive();
    },
  },
  methods: {
    get_projects(service_id) {
      api.projectApi.get_projects({ service_id }).then((result) => {
        if (result && result.success) {
          this.projectList = result.msg;
          localStorage.setItem('projectList', JSON.stringify(result.msg));
          localStorage.setItem('service_id', service_id);
        }
      });
    },
    toActive() {
      const to = this.$route;
      this.service_id = (to.path.indexOf('bsc') > -1) ? 2 : 1;
      this.get_projects(this.service_id);
    },
  },
  mounted() {
    this.toActive();
  },


};
</script>

